	module.exports = {"data":{"markdownRemark":{"html":"<h2>Continuous Deployment</h2>\n<p>Continuous Deployment is a modern software engineering process which is considered part of a DevOps environment. It involves teams of developers producing, updating and releasing code in very short cycles. This means developers commit smaller amounts of code, much more often.</p>\n<p>The goal of Continuous Deployment is to have code in a constant reliable and deployable state to enable this code to be released at any time. This process aims to release code more quickly. To achieve continuous deployment, a development team relies on infrastructure that automates and instruments the various steps leading up to deployment. This is quite often called Infrastructure as Code (IaC). </p>\n<p>Two main benefits of Continuous Deployment include an earlier return on investment for each feature after it is developed due to the lower release times as well as earlier feedback on new features. </p>\n<p>Other benefits to Continuous Deployment include improved code quality due to less bugs making it to production, more reliable code releases and a much lower time to market.</p>","fields":{"slug":"/agile/continuous-deployment/"},"frontmatter":{"title":"Continuous Deployment"}}},"pathContext":{"slug":"/agile/continuous-deployment/"}}

